

.addcomponent{
    margin-top: 70px;
    /* text-align: center; */
    margin-left: 300px;
}
.input1{
    margin-left: 10px;
    margin-right: 10px;
    width:20%;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    padding: 12px 20px; 
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  
    font-size: 20px;
    background-color:antiquewhite;
    border-color: pink;
    border-width: 5px;
}
.input2{
    margin-left: 10px;
    margin-right: 10px;
    width:30%;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    padding: 12px 20px;  
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
    border-color: pink;
    border-width: 5px;
    background-color:antiquewhite;
}
.addcomponent button{
    margin-left: 10px;
    margin-right: 10px;
    width:10%;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    padding: 12px 20px; 
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  
    font-size: 20px;
    background-color:rgb(243, 229, 229);
    border-color: lightgreen;
    border-width: 5px;
}
.addcomponent button:hover{
    background-color: darkseagreen;
}

