.todo{
   border:1px solid;
   margin-left: 10px;
   margin-right: 10px;
   margin-top: 10px;
   padding: 1px;
   background-color:rgb(240, 188, 188);
   height:5%;
   border-style: groove;
   border-radius: 5px;

}
.todo h4{
    color:rgb(190, 55, 10);
    font-weight: 500;
    font-size: 22px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding-left: 10px;
}
.container{
    display: flex;

}
.container button{
    
    height: 40px;
    margin-top: 5px;
    background-color: rgb(240, 188, 188);
    border: 0px;
    font-size: 40px;
}

.desc{
    padding-left: 30px;   
    padding-right: 30px; 
    font-size: 17px;  
    font-family: Georgia, 'Times New Roman', Times, serif;
    
}
